import axios from 'axios'

const state = {
  productItems: []
};

const mutations = {
  UPDATE_PRODUCT_ITEMS (state, productItems) {
    state.productItems = productItems;
  }
};

const actions = {
  getProductItems (context) {
    // 从服务获取所有商品信息
    axios.get('/api/product').then(function(response){
      // 通知 UPDATE_PRODUCT_ITEMS 方法gen更新 state.productItems
      context.commit('UPDATE_PRODUCT_ITEMS', response.data)

      /* eslint-disable */
      console.log(response)
      /* eslint-enable */
    })
  }
};

const getters = {
  productItems: state => state.productItems
};

const productModule = {
  state,
  mutations,
  actions,
  getters
};

export default productModule;
