import Axios from "axios";

const state = {
  cartItems: []
};

const mutations = {
  UPDATE_CART_ITEMS(state, cartItems) {
    state.cartItems = cartItems
  }
};

const actions = {
  addToCart(context, productItem) {
    Axios.post('/api/cart', productItem).then(response => {
      context.commit('UPDATE_CART_ITEMS', response.data)
    })
  },
  getCartItems(context) {
    Axios.get('/api/cart').then(response => {
      context.commit('UPDATE_CART_ITEMS', response.data)
    })
  },
  removeCartItem(context, cartItemId) {
    Axios.delete('/api/cart/delete', {params: {id: cartItemId}}).then(response => {
      context.commit('UPDATE_CART_ITEMS', response.data)
    })
  },
  removeAllCartItems(context) {
    Axios.delete('/api/cart/delete/all').then(response => {
      context.commit('UPDATE_CART_ITEMS', response.data)
    })
  }
};

const getters = {
  cartItems: state => state.cartItems,
  cartTotal: state => {
    return state.cartItems.reduce((total, cartItem) => {
      /* eslint-disable */
      console.log((cartItem.quantity * cartItem.price), total)
      /* eslint-enable */
      return (cartItem.quantity * cartItem.price) + total
    }, 0).toFixed(2)
  },
  cartQuantity: state => {
    return state.cartItems.reduce(function(total, cartItem){
      return cartItem.quantity + total
    }, 0)
  }
};

const cartModule = {
  state,
  mutations,
  actions,
  getters
};

export default cartModule;
