<template>
  <div class="columns is-gapless is-multiline">
      <div class="column is-full">
        <span>{{cartItem.title}}</span>
        <span class="is-pulled-right">
          <i class="fa fa-arrow-circle-up operate" @click="addToCart(cartItem)"></i>
          <i class="fa fa-arrow-circle-down operate" @click="removeCartItem(cartItem.id)"></i>
        </span>
      </div>
      <div class="column is-full">
        <span class="has-text-primary has-text-weight-bold">单价：{{cartItem.price}}</span>
        <span class="has-text-grey is-pulled-right">数量：{{cartItem.quantity}}</span>
      </div>
  </div>
</template>

<script>
export default {
  name: "CartListItem",
  props: ["cartItem"],
  methods: {
    addToCart(cartItem) {
      this.$store.dispatch('addToCart', cartItem)
    },
    removeCartItem(cartItemId) {
      this.$store.dispatch('removeCartItem', cartItemId)
    }
  }
};
</script>

