<template>
  <div class="box">
    <div class="product-header has-text-centered">
      <i class="fa fa-user-circle fa-2x"></i>
    </div>
    <div class="product-list">
      <ProductListItem v-for="productItem in productItems" :key="productItem.id" :productItem="productItem"></ProductListItem>
    </div>
    <div class="product-count has-text-right has-text-weight-bold">
      总商品数量：{{productItems.length}}
    </div>
  </div>
</template>
<script>
import ProductListItem from './ProductListItem.vue'
export default {
  name: 'ProductList',
  created () {
    this.$store.dispatch('getProductItems')
  },
  computed: {
    productItems () {
      return this.$store.getters.productItems
    }
  },
  components: {
    ProductListItem
  }
}
</script>
<style lang="less" scoped>
.product-header {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 15px;
}
</style>


