<template>
  <div class="product-list-item">
    <h2 class="is-size-5 has-text-centered">
      {{productItem.title}}
      <span class="button is-small is-primary is-pulled-right" @click="addToCart(productItem)">加入购物车</span>
      </h2>
    <p>{{productItem.description}}</p>
    <p class="has-text-primary has-text-weight-bold">${{productItem.price}}</p>
  </div>
</template>
<script>
export default {
  name: 'ProductListItem',
  props: ['productItem'],
  methods: {
    addToCart(productItem) {
      this.$store.dispatch('addToCart', productItem)
    }
  }
}
</script>
<style lang="less" scoped>
.product-list-item {
  padding: 10px 0;
}
</style>


