<template>
  <div class="columns is-multiline">

    <div class="column is-full">
      <div class="cart-header has-text-centered">
        <i class="fa fa-user-circle fa-2x"></i>
      </div>
    </div>

    <div class="column is-full">
      <CartListItem  v-for="cartItem in cartItems" :key="cartItem.id" :cartItem="cartItem"></CartListItem>
    </div>
  
    <div class="column is-full">
      <span>总数量：<strong>{{cartQuantity}}</strong></span>
      <span class="is-pulled-right" @click="removeAllCartItems"><i class="fa fa-trash"></i>清空购物车</span>
    </div>

    <div class="column is-full">
      <button class="button is-fullwidth is-primary">￥{{cartTotal}}</button>
    </div>
  </div>
</template>

<script>
import CartListItem from './CartListItem.vue'
export default {
  name: 'CartList',
  created() {
    this.$store.dispatch('getCartItems')
  },
  methods: {
    removeAllCartItems() {
      this.$store.dispatch('removeAllCartItems')
    }
  },
  computed: {
    cartItems() {
      return this.$store.getters.cartItems
    },
    cartTotal() {
      return this.$store.getters.cartTotal
    },
    cartQuantity() {
      return this.$store.getters.cartQuantity
    }
  },
  components: {
    CartListItem
  }
}
</script>

